.mtl-tree .tree-node-inner {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.mtl-tree .tree-node {
  padding: 1px 0;
}

.mtl-tree .tree-node:hover {
  background-color: #ededed;
  /* recommend: active #ddeff9, active & hover: #cfe6f2 */
}

.mtl-checkbox {
  width: 14px;
  height: 14px;
}

.mtl-ml {
  margin-left: 4px;
}

.mtl-mr {
  margin-right: 4px;
}

.mtl-tree table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}


.mtl-tree td,
.mtl-tree th {
  border-bottom: 1px solid rgba(224, 224, 224, 1);
  line-height: 1.5;
}

.mtl-tree tr:last-child td,
.mtl-tree tr:last-child tr {
  border-bottom: 0px;
}

.mtl-text-left {
  text-align: left;
}

.mtl-text-center {
  text-align: center;
}

.mtl-text-right {
  text-align: right;
}